import React, { Component } from 'react';
import logo from './logo.svg';
import Img from './pp.jpg';
import './App.css';
import './index.css';

import { Navigation, Main } from './components';

class App extends Component {


  render() {
    return (
      <div>
      <div className="bgHero">
      <img className="ss-profile-img" src={Img} alt="DineshKumar"/>
      <div className="header-lable-name">
        <p className="font-medium text-2xl leading-normal pb-2 text-white">Hi, I'm Dinesh, I am a Creative and self-starting 
        front-end web developer.</p></div>
        </div>
      <main>
        <Navigation />
        <Main />
      </main>
      </div>
    );
  }
}

export default App;



