import React from 'react';

import { NavLink } from 'react-router-dom';

const Navigation = () => {
    return (
        <nav>
          {/* <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
            <span className="navbar-toggler-icon"></span>
          </button> */}
            <ul>
              <li>
                    <NavLink to='/' >Home</NavLink>
                    {/* <NavLink to={`${process.env.PUBLIC_URL}/`} >Home</NavLink> */}
              </li>
              <li className="ml1">
                    <NavLink to='/about'  activeClassName="selectedLink">About</NavLink>
                    {/* <NavLink to={`${process.env.PUBLIC_URL}/about`}>About</NavLink> */}
              </li>
              <li className="ml1">
                  <NavLink to='/contact'  activeClassName="selectedLink">Contact</NavLink>
                  {/* <NavLink to={`${process.env.PUBLIC_URL}/contact`}>Contact</NavLink> */}
              </li>
            </ul>
            <style>
				{`
					nav {
						width: 100%;
						height: 3rem;
					}
					nav,
					ul {
						display: flex;
						list-style: none;
						margin: 0;
					}
					a {
						text-decoration: none;
						color: #666666;
						background-image: none;
					}
					a:focus {
						box-shadow: none;
						border-bottom: 0.05rem solid #eaeaea;
						border-radius: 0;
					}
					.ml1 {
						margin-left: 1rem;
					}
					.selectedLink {
						border-bottom: 0.05rem dotted #000000;
						color: #000000;
					}
				`}
			</style>
        </nav>
    );
}

export { Navigation };



